.product__content {
  display: block;
  justify-content: center;
  align-items: center;

  @media screen and ($mobile-min) {
    display: block;
    align-items: center;
  }
}

.product__label {
  @include font(800, 18px, 1.22);
  text-align: center;
  text-transform: uppercase;
  color: var(--primary-color);

  @media screen and ($mobile-min) {
    padding-bottom: 12px;
  }
}

.product__title {
  font-family: 'Seymour One';
  @include font(400, 20px, 1.36);
  color: var(--primary-color);
  padding-bottom: 168px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media screen and ($mobile-min) {
    font-weight: 400;
    font-size: 30px;

    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding-bottom: 164px;
  }

  @media screen and ($tablet-min) {
    padding-bottom: 200px;
  }

  @media screen and ($desktop-min) {
    font-size: 36px;
    padding-bottom: 303px;
  }
}

.grid {
  display: flex;
  align-items: center;
  flex-direction: column;

  @media screen and ($mobile-min) {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media screen and ($tablet-min) {
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: center;
    align-items: baseline;
  }
}

.grid__item::before {
  position: absolute;
  // width: 100%;
  // max-width: 200px;
  // height: 200px;
  background-size: contain;
  background-repeat: no-repeat;
  display: block;
  top: -35%;
  left: 15%;
  translate: (35%, -15%);
  border-radius: 50%;
}

.grid__item::before {
  position: absolute;
  height: 200px;
  display: block;
  top: -100px;
  left: 40px;
  border-radius: 50%;
}

.grid__item:nth-child(1)::before {
  content: url(../images/products/product1-s.png);
  box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
}

.grid__item:nth-child(2)::before {
  content: url(../images/products/product2-s.png);
  box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
}

.grid__item:nth-child(3)::before {
  content: url(../images/products/product3-s.png);
  box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
}

@media screen and (min-device-pixel-ratio: 2) and ($mobile-min),
  screen and (min-resolution: 192dpi) and ($mobile-min),
  screen and (min-resolution: 2dppx) and ($mobile-min) {
  .grid__item::before {
    width: 200px;
    height: 200px;
    background-size: contain;
  }

  .grid__item:nth-child(1)::before {
    content: '';
    background-image: url(../images/products/product1-s@2x.png);
  }

  .grid__item:nth-child(2)::before {
    content: '';
    background-image: url(../images/products/product2-s@2x.png);
  }

  .grid__item:nth-child(3)::before {
    content: '';
    background-image: url(../images/products/product3-s@2x.png);
  }
}

@media screen and ($tablet-min) {
  .grid__item::before {
    width: 200px;
    height: 197px;
    top: -120px;
    left: 12px;
  }

  .grid__item:nth-child(1)::before {
    content: '';
    background-image: url(../images/products/product1-m@2x.png);
  }

  .grid__item:nth-child(2)::before {
    content: '';
    background-image: url(../images/products/product2-m@2x.png);
  }

  .grid__item:nth-child(3)::before {
    content: '';
    background-image: url(../images/products/product3-m@2x.png);
  }
}

@media screen and (min-device-pixel-ratio: 2) and ($tablet-min),
  screen and (min-resolution: 192dpi) and ($tablet-min),
  screen and (min-resolution: 2dppx) and ($tablet-min) {
  .grid__item:nth-child(1)::before {
    content: '';
    background-image: url(../images/products/product1-m@2x.png);
  }

  .grid__item:nth-child(2)::before {
    content: '';
    background-image: url(../images/products/product2-m@2x.png);
  }

  .grid__item:nth-child(3)::before {
    content: '';
    background-image: url(../images/products/product3-m@2x.png);
  }
}

@media screen and ($desktop-min) {
  .grid__item::before {
    width: 300px;
    height: 300px;
    top: -150px;
    left: 35px;
  }

  .grid__item:nth-child(1)::before {
    content: '';
    background-image: url(../images/products/product1-l.png);
  }

  .grid__item:nth-child(2)::before {
    content: '';
    background-image: url(../images/products/product2-l.png);
  }

  .grid__item:nth-child(3)::before {
    content: '';
    background-image: url(../images/products/product3-l.png);
  }
}

@media screen and (min-device-pixel-ratio: 2) and ($desktop-min),
  screen and (min-resolution: 192dpi) and ($desktop-min),
  screen and (min-resolution: 2dppx) and ($desktop-min) {
  .grid__item:nth-child(1)::before {
    content: '';
    background-image: url(../images/products/product1-l@2x.png);
  }

  .grid__item:nth-child(2)::before {
    content: '';
    background-image: url(../images/products/product2-l@2x.png);
  }

  .grid__item:nth-child(3)::before {
    content: '';
    background-image: url(../images/products/product3-l@2x.png);
  }
}

.grid__item {
  &:first-child > .production {
    background: var(--palette-color-1);
  }

  &:nth-child(2) > .production {
    background: var(--palette-color-2);
  }

  &:nth-child(3) > .production {
    background: var(--palette-color-3);
  }

  position: relative;

  &::before {
    background-size: contain;
  }
}

.grid__item {
  align-items: baseline;

  @media screen and ($mobile-max) {
    &:not(:last-child) {
      margin-bottom: 164px;
    }
  }

  @media screen and ($tablet-min) {
    &:not(:last-child) {
      margin-right: 16px;
    }
  }
}

.production {
  border-radius: 24px;
  width: 100%;
  max-width: 280px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  justify-content: start;

  padding-top: 123px;

  @media screen and ($mobile-min) {
    max-width: 280px;
    height: 420px;
    // left: 20px;
    // top: 782px;
    // border-radius: 24px;
    padding-top: 123px;
    padding-right: 40px;
    padding-left: 40px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    // flex-grow: 1;
    // align-items: baseline;
    // height: 100%;
    // justify-content: end;
  }

  @media screen and ($tablet-min) {
    max-width: 224px;
    height: 428px;
    // padding: 166px 61px 60px;
    padding-top: 106px;
    padding: 106px 26px 20px;
  }

  @media screen and ($desktop-min) {
    max-width: 370px;
    height: 535px;
    padding: 166px 61px 60px;
  }
}

.grid__item:first-child > .production__description {
  padding-bottom: 10px;
}

.production__name {
  @include font(700, 20px, 1.2);
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--primary-color);
  margin-bottom: 40px;
  text-align: center;

  @media screen and ($mobile-min) {
    font-size: 30px;
  }

  @media screen and ($tablet-min) {
    margin-bottom: 40px;
    font-size: 22px;
  }

  @media screen and ($desktop-min) {
    font-size: 30px;
    margin-bottom: 65px;
  }
}

.production__description {
  @include font(400, 14px, 1.76);
  text-align: center;
  letter-spacing: 0.04em;
  color: var(--primary-color);

  @media screen and ($mobile-min) {
    margin-bottom: 55px;
  }

  @media screen and ($tablet-min) {
    margin-bottom: 30px;
  }

  @media screen and ($desktop-min) {
    font-size: 16px;
    margin-bottom: 65px;
  }
}

.products__button {
  padding: 10px 0;
  border: none;
  @include font(600, 8px, 10px);
  fill: currentColor;
  background-color: transparent;
  transition: color var(--transition-function), width var(--transition-function),
    height var(--transition-function);
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);

  @media screen and ($desktop-min) {
    bottom: 60px;
  }

  &:hover .button__icon,
  &:focus .button__icon {
    width: 50px;
  }

  & .products__arrow-default {
    opacity: 1;
  }
  & .products__arrow-hover,
  & .products__arrow-focus {
    opacity: 0;
  }
}
.products__button:hover {
  & .products__arrow-hover {
    opacity: 1;
  }
  & .products__arrow-default,
  & .products__arrow-focus {
    opacity: 0;
  }
}
.products__button:focus {
  & .products__arrow-focus {
    opacity: 1;
  }
  & .products__arrow-default,
  & .products__arrow-hover {
    opacity: 0;
  }
}

.button__icon {
  width: 40px;
  height: 17px;
  pointer-events: none;
  transition: color var(--transition-function), width var(--transition-function),
    height var(--transition-function);
}
